import '../libs/mobiscroll/mobiscroll'

var mobiscrollDate = {
    bind:function(){
        var self = this
        $(this.el).mobiscroll().date({
            theme:'mobiscroll',
            dateFormat: 'yy-mm-dd',
            mode:'scroller',
            lang: 'zh',
            maxDate:new Date(),
            display:'bottom'
        })
    },
    update:function(value){
        $(this.el).mobiscroll('setVal', value)
    }
}

var mobiscrollSelectBasic = {
    bind:function(){
        $(this.el).mobiscroll().select({
            theme:'mobiscroll',
            mode:'scroller',
            lang: 'zh',
            display:'bottom'
        })
    },
    update:function(value){
        // mobiscorllselectmobiscorllinputdomvalue
        $(this.el).prev().val(value)
    }
}

var mobiscrollSelectGroup = {
    bind:function(){
        $(this.el).mobiscroll().select({
            theme:'mobiscroll',
            lang: 'zh',
            display: 'bottom',
            label: 'City',
            group: true,
            groupLabel: 'Province',
            fixedWidth: [100, 170]
        })
    },
    update:function(value){
        // mobiscorllselectmobiscorllinputdomvalue
        $(this.el).prev().val(value)
    }
}

export { mobiscrollDate, mobiscrollSelectBasic, mobiscrollSelectGroup }




